<template>
    <swiper :options='swiperOption'>
        <swiper-slide><img style="width:100% ;height:100%" src="../../../assets/icon/001.jpg"  alt="" srcset=""></swiper-slide>
        <swiper-slide><img style="width:100% ;height:100%" src="../../../assets/icon/002.jpg" alt="" srcset=""></swiper-slide>
        <swiper-slide><img style="width:100% ;height:100%" src="../../../assets/icon/001.jpg" alt="" srcset=""></swiper-slide>
        <swiper-slide><img style="width:100% ;height:100%" src="../../../assets/icon/002.jpg" alt="" srcset=""></swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
</template>

<script>

export default {
     data () {
      return {
        swiperOption: {
            autoplay : true,
            loop: true,
            pagination : '.swiper-pagination',
          // some swiper options/callbacks
          // 所有的参数同 swiper 官方 api 参数
          // ...
        }
      }
    },
    computed: {
      
    },
    mounted() {
    //  this.swiper.slideTo(3, 1000, false);   
    }
     
}

</script>

<style scoped>
*{
    margin: 0;
    padding: 0;
}
.swiper-container {
    width: 100%;
    height: 500px;
}
img{

} 
</style>



