<template>
    <div>
        <div id="container" style="overflow:hidden">
            <ul>
                    <li v-for="(item,index) in items" :key="index">
                    <router-link tag="div" :to="`/introduce/leader/`+item.id">
                    <img src="https://dummyimage.com/250x170/000/red" alt="" />
                    <div class="desc">
                        <span class="name">{{ item.name }}</span>
                        <span class="profess">{{ item.profess }}</span>
                        <span class="intro">{{ item.intro|introFilter }}
                        </span>
                        
                    </div>
                    </router-link>
                </li>
            </ul>
           
        </div>
         <div  style="text-align:center;font-size:18px;color:666666">{{ loading }}</div>
    </div>
</template>

<script>
export default {
    name:"Leader",
    data(){
        return {
            items:[],
            isShow:false,
            dataLength:'',
            loading:'下拉刷新'
        }
    },
    mounted() {
        window.addEventListener('scroll',this.handleScroll)
        this.$http.get('/static/lead.json').then((res)=>{
            let data =res.data.data
            this.dataLength = data.length
            this.items=data.slice(0,4)
        })
    },
    filters:{
        introFilter(value) {
            if(value.length>100){
                return value.slice(0,50)+'...'
            }else {
                return value
            }
        }
    },
    methods: {
        handleScroll(){
            if(this.dataLength == this.items.length){
                this.loading = '到底了...'
            }else{
                this.$http.get('/static/lead.json').then((res)=>{
                    let data= res.data.data
                    if(document.body.scrollHeight-document.documentElement.scrollTop == document.documentElement.clientHeight){
                        let res1 = data.slice(this.items.length,this.items.length+4)
                        this.items=this.items.concat(res1)
                }
            }) 
        }
    }
}
}
</script>

<style scoped>
#container{
    margin-top: -10px
}
ul {
    width: 770px;
    list-style: none;
    padding: 0px;
}
ul li{
    margin: 15px auto;
    clear: both;
}
ul li .desc{
    padding:20px;
    float: right;
    display: inline-block;
    width: 480px;
    height: 130px;
    background-color:#eee;
}
ul li .desc .name{
    display: inline-block;
    font-family: "Microsoft YaHei";
    font-size: 28px;
}
ul li .desc .profess{
    /* margin: -25px; */
    font-size: 14px;
}
ul li .desc .intro{
    display: block;
    font-family: "Microsoft YaHei";
    font-size: 14px;
    margin-top:20px; 
}
</style>


