<template>
<div style="width:100%">
     <div class="header">
        <div style="overflow:hidden:height:100px">
            <div class="imgLogo">
            <router-link to="/">
                <img class="logo" src='../../assets/icon/logo.png' alt="崇杰律师事务所logo">
            </router-link>
        </div>
          <div class="ula">
              <ul>
              <li class="one">
                  <router-link tag="span" to="/">
                      首页
                  </router-link>
                  </li>
              <li 
              class="one"
              @mouseover="handleOver"
              @mouseleave="isMouseOver = false"
              
              >
                  <router-link 
                  tag='span' 
                  to='/introduce/office' 
                  :class="{limouseover: isMouseOver}"
                  style="padding-bottom:10px"
                  >介绍
                  </router-link>
                  <ul>
                    <li><router-link tag="span" to='/introduce/office'>律师事务所</router-link></li>
                    <li><router-link tag="span" to="/introduce/leader">团队介绍</router-link></li>
                    <li><router-link tag="span" to="/introduce/Collection/0">团队文集</router-link></li>
                </ul>
            </li>
            <li class="one"
            @mouseover="handleOverserver" 
                    @mouseleave="isMouseOverserver = false"
            >
                    <router-link 
                    tag='span'
                    :class="{limouseover: isMouseOverserver}" 
                    to='/server' 
                    style="padding-bottom:10px"
                    >公司法律服务</router-link>
                <ul>
                    <li><router-link to="/server/equityeightesign/1" tag="span">股权设计</router-link></li>
                    <li>
                        <router-link to="/server/equityincentive" tag="span">
                            股权激励
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/server/equityallocationtable" tag="span">
                            报价配置表
                        </router-link>
                        </li>
                </ul>
            </li>
            <li class="one"
               @mouseover="handleOverPe" 
            @mouseleave="isMouseOverP = false" 
            >
                <router-link 
                to="/penallaw"
                tag="span"
                :class="{limouseover: isMouseOverP}"
                style="padding-bottom:10px"
                >
                    刑事法律服务
                </router-link>
                <ul>
                    <li><router-link to="/penallaw/lawser/1" tag="span">刑事服务</router-link></li>
                    <li><router-link to="/penallaw/advan" tag="span">优势服务</router-link></li>
                </ul>
            </li>
            <li class="one"
                @mouseover="handleOverLab" 
                @mouseleave="isMouseOverlab = false"
                style="position:relative"
            >
                <router-link 
                to="/labour/reply/0" 
                tag="span" 
                :class="{limouseover: isMouseOverlab}"
                style="padding-bottom:10px"
                >劳动法律服务</router-link>
                <ul class="moveIt" style="position:absolute">
                    <li><router-link to="/labour/reply/1" tag="span">诉讼与仲裁应对</router-link></li>
                    <li><router-link to="/labour/design" tag="span">劳动用工制度事先设计</router-link></li>
                  <li><router-link to="/labour/select" tag="span">企业文化与策略选择</router-link></li>
                </ul>
            </li>
            <span class="headerSearch" v-show="isShow">
                <img style="width:18px;height:18px" src="../../assets/icon/kc_sea_ico11.png"
                alt=""
                @click="onHandleClick">
            </span>
            <span class="headerSearch" v-show="!isShow">
                <img style="width:18px;height:18px" src="../../assets/icon/kc_sea_ico21.png"
                alt=""
                @click="onHandleClickT"
                >
            </span>
          </ul>
          </div>
        </div>
          <div class="inputSearch" v-show="!isShow">
            <div>
             <span>
                    <input class="inputS" type="text" placeholder="请输入搜索内容">
                    <button class="search" >
                        搜索
                    </button>
            </span>
            </div>
        </div>
    </div>
</div>
</template>

<script>

export default {
    data(){
        return {
            isShow:true,
            isMouseOver: false,
            isMouseOverserver:false,
            isMouseOverP:false,
            isMouseOverlab:false
        }
    },
    methods:{
        onHandleClick(){
            this.isShow = false
        },
        onHandleClickT(){
            this.isShow = true
        },
        handleOver () {
            this.isMouseOver = true
        },
        handleOverserver() {
            this.isMouseOverserver=true
        },
        ins() {
            alert('yep')
        },
        handleOverPe() {
            this.isMouseOverP=true
        },
        handleOverLab() {
            this.isMouseOverlab=true
        }
    }
}
</script>

<style scoped>

* {
    padding: 0;
    margin: 0;
}
.headerSearch{
    float: right;
    margin-left: 10px;
    padding-top: 5px;
}
.header{
    width: 1200px;
    height: 115px;
    margin: 0 auto;
    text-align: center;
    vertical-align: middle;
}
.inputSearch{
    width:100%;
    position: absolute;
    z-index: 2;
    top: 100px;
    height: 45px;
    margin: 0 auto;
    background: rgba(72,73,75,0.5);
    left: 0px;
}
.inputSearch div{
    left: 800px;
    position: relative;
    padding-top:6px;
}
.search{
    height: 32px;
    width: 79px;
    position: relative;
    border: none;
    color: #fff;
    background: #bc9244;
    float: left;
    bottom: 1px;
}
.inputS{
    height: 30px;
    float: left;
    width: 200px;
    border: 0px;
    padding-left: 5px
}
input::-webkit-input-placeholder{
    color: #bc9244;
}
.ula{
    float: right;
    margin-bottom:12px; 
    height: 30px;
    margin-top: 35px;
    
}

ul {
    list-style: none
}
ul li {
    cursor: pointer;
}

.one{
    position: relative;
    float: left;
    color: black;
    font-size: 18px;
    font-family:"Microsoft YaHei";
    padding: 3px 6px;
    
}
.one ul {
    display: none;
    position: absolute;
    left: 0;
    top: 36px;
    z-index: 10;
    width: 500px;
    
}
.one li{
    float: left;
     font-size: 16px;
    font-family:"Microsoft YaHei";
    color: black;
    padding: 10px 6px;
}
.imgLogo{
    display: inline-block;
    margin-top: 28px;
    float: left;
}
.limouseover {
    border-bottom: 2px solid #b1976b
}

ul li:hover ul{
    display: block;
            
}
ul li:hover .moveIt{
    display: block;
    position: absolute;
    left: -327px;
    }
/* .move{
    position: absolute;
    top: 2200px;
} */


/* ul li ul{
            position: absolute;
            padding-left:0px; 
            display: none
}

} */

</style>



