<template>
    <div>
        <Header></Header>
        <div class="pic">

        </div>
        <div class="container">
            <div class="sidebar">
                <ul>
                    <router-link 
                    to="/penallaw/lawser/0" 
                    tag="li" 
                    style="font-size:18px;font-family:'Microsoft YaHei'"
                    :style="{background:this.$route.path == '/penallaw/lawser/0'?'#4d4d4d':'',color:this.$route.path == '/penallaw/lawser/0'?'#fff':''}"
                    >
                        刑事法律服务
                    </router-link>
                    <router-link 
                    to="/penallaw/lawser/1" 
                    tag="li" 
                    class="seMenu"
                    :style="{background:this.$route.path == '/penallaw/lawser/1'?'#4d4d4d':'',color:this.$route.path == '/penallaw/lawser/1'?'#fff':''}"                    
                    >
                        刑事服务
                    </router-link>
                    <router-link 
                    to="/penallaw/advan" 
                    tag="li" 
                    class="seMenu"
                    :style="{background:this.$route.path == '/penallaw/advan'?'#4d4d4d':'',color:this.$route.path == '/penallaw/advan'?'#fff':''}"
                    >
                        优势
                    </router-link>
             </ul>
            </div>
            <div class="content">
                <router-view />
            </div>
        </div>
        <Float></Float>
        <Footer></Footer>
    </div>
</template>

<script>
import Header from '../../common/Header/Header'
import Footer from '../../common/Footer/footer'
import Lawser from './components/Lawser'
import Advan from './components/Advan'
import Float from '../.././common/Float/Float'
export default {
    data () {
       return {
            menuId: 0
       }
    },
    methods: {
        handleClick (id) {
            this.$router.push('/penallaw/lawser/'+id)
            this.menuId = id
        }
    },
    components:{
        Header,
        Lawser,
        Advan,
        Footer,
        Float
    }
}
</script>

<style scoped>
.container{
    width: 1200px;
     margin: 20px auto;
     padding-top:20px;
    display: flex;
}
.seMenu{
    font-size: 16px;
    font-family: "Microsoft YaHei"
}
.sidebar{
    width: 280px;
}
.sidebar li{
    list-style: none;
    padding: 10px;
    background-color: #eee;
    text-align: center;
    cursor: pointer;
}
.sidebar li:hover{
    background-color: #4d4d4d;
    color: white;
}

.liActive {
    background-color: #4d4d4d;
}

.content {
    flex: 1;   
    margin-left: 50px
}

.pic{
    width: 100%;
    height: 200px;
    background-color: #eee;

}
ul{
    padding-left: 0px
}
</style>



